<template>
  <el-container>
  <el-tabs v-model="activeName" >
    <el-tab-pane label="算法实现" name="first">
      <MarkdownReader :url="url"></MarkdownReader>
    </el-tab-pane>
    <el-tab-pane label="生成数据" name="second">
      <el-container>
        <el-form ref="form" :model="form" label-width="80px" :inline="true">
          <el-form-item label="N">
              <el-input v-model="form.N"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="onSubmit">立即创建</el-button>
            <el-button>取消</el-button>
          </el-form-item>
      </el-form>
      </el-container>
      <el-container>
        <!-- <DataTable :books="books"></DataTable> -->
      </el-container>
    </el-tab-pane>
    
    
  </el-tabs>
  </el-container>
</template>
<script>
  export default {
    data() {
      return {
        activeName: 'first',
        url: "genrator",
        method:'quickSort',
        form:{},
        books:[,
          {
            "bookName":"123",
            "bookId":"123123",
            "author":"asdasd",
            "monthTicket":100
          }
        ]
      };
    },
    methods: {
      onSubmit(){
        this.$axios.get('/api/genrator?num='+this.form['N']).then((resp)=>{
          this.$alert(`任务创建成功，UUID为${resp.data.uuid}`,'通知')}).catch((resp)=>{
            this.$message({type:'danger',message:'404错误'})
          })
        
      }
    },
  };
</script>




